// SCSS файл с основным (корневыми) настройками стилей
@import './normolize.scss'; // Подключение normolize
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap'); // Подключение шрифтов
// Отключение боковой прокрутки
html {
    overflow-x: hidden;
}
body {
    overflow-x: hidden;
}
// Центрирование контейнера
.container {
    margin: 0 auto;
    max-width: 1200px;
    padding: 0 24px;
}
// Переменные цветов
// colors
$white: #FFFFFF;
$grey: #888888;
$black: #000000;